                   </span>
                </div>
                <a-button type="primary" class="renew-btn" :loading="iconLoading" @click="goToRenew">
                  {{ $t('my/service:plan.Button-5') }}
                </a-button>
            </div>
            <div class="card-box-bg flow-detail-box">
                <div class="flow-left flow-right">
                    <span class="number-span"> {{ selectedItem.also_traffic }}<span class="unit-span">GB</span></span>
                    <span class="sec-row">{{ $t('my/service:plan.Text-1') }}</span>
                    <span class="reset-date"> {{ selectedItem.reset_time }} {{ $t('my/service:plan.Countdown-2') }}</span>
                </div>
                <div class="flow-right">
                    <span class="number-span">{{ selectedItem.speed_limit ? selectedItem.speed_limit : '∞ ' }}
                      <span class="unit-span">M</span>
                    </span>
                    <span class="sec-row">{{ $t('my/service:plan.Text-4') }}</span>
                    <span class="limit-device">限制设备 不限</span>
                </div>
            </div>
            <div class="card-box-bg flow-detail-box bill-detail-box">
                <div class="flow-left flow-right">
                    <span class="number-span">{{ selectedItem.money }} <span
                        class="unit-span">元</span></span>
                    <span class="sec-row">{{ $t('my/service:plan.Text-5') }}</span>
                  <a-button>{{ $t('my/service:bill.Text-8') }}</a-button>
                </div>
            </div>
        </div>
        <div class="my-bill-box" v-if="isPc">
            <a-row>
              <a-col :span="8">
                <span class="title">{{ $t('my/service:bill.Text-1') }}</span>
              </a-col>
              <a-col style="float: right">
                <a-button type="primary" :disabled="!prepaidPlan" class="renew-btn" @click="showConfirm(item)">
                  {{ $t('my/service:bill.Button-3') }}
                </a-button>
              </a-col>
            </a-row>
            <a-divider style="background-color: #E5E9ED"/>
            <div class="header-row">
                <span class="header-item">{{ $t('my/service:bill.Text-2') }}</span>
                <div class="header-item span-cursor">
                    <div class="type-group">
                        <div class="type-title-row">
                            <span>{{ $t('my/service:bill.Text-3') }}</span>
                            <i class="type-normal" :class="{'type-checked':status.length > 0}"></i>
                        </div>
                    </div>
                </div>
                <div class="header-item span-cursor">
                    <div class="date-group">
                        <div class="type-title-row">
                            <span>{{ $t('my/service:bill.Text-4') }}</span>
                            <i class="type-normal" :class="{'type-checked':status.length > 0}"></i>
                        </div>
                    </div>
                </div>
                <div class="header-item span-cursor">
                  <div class="date-group">
                    <div class="type-title-row">
                      <span>{{ $t('my/service:bill.Text-11') }}</span>
                      <i class="type-normal" :class="{'type-checked':status.length > 0}"></i>
                    </div>
                  </div>
                </div>
                <div class="header-item span-cursor"  style="width: 10%">
                    <span>{{ $t('my/service:bill.Text-5') }}</span>
                    <div class="right-up-down">
                        <i class="up-i"
                           :class="{'up-blue-i': sortBy === 'EffectiveDateTime' && sortType === 'Asc'}"></i>
                        <i class="down-i"
                           :class="{'down-blue-i': sortBy === 'EffectiveDateTime' && sortType === 'Desc'}"></i>
                    </div>
                </div>
                <div class="header-item span-cursor" style="width: 10%">
                    <div class="type-group">
                        <div class="type-title-row">
                            <span>{{ $t('my/service:bill.Text-6') }}</span>
                            <i class="type-normal" :class="{'type-checked':status.length > 0}"></i>
                        </div>
<!--                        <div class="type-box">-->
<!--                            <div class="type-row" v-for="(item,index) in payStatusList" :key="index">-->
<!--                                <a-checkbox :checked="item.ischecked" @change="payStatusChange(item)">{{item.key}}</a-checkbox>-->
<!--                            </div>-->
<!--                        </div>-->
                    </div>
                </div>
                <span style="text-align:right;width:10%">{{ $t('my/service:bill.Text-7') }}</span>
            </div>
            <a-divider style="background-color: #E5E9ED;margin-bottom: 0px;"/>
            <div class="loading" v-if="loading">
                <a-spin>
                    <a-icon slot="indicator" type="loading" :style="{'font-size': 80 + 'px'}" spin/>
                </a-spin>
            </div>
            <div class="header-row item-row" v-for="(item,index) in billList" :key="index">
                <span class="header-item">{{ item.sn }}</span>
                <span class="header-item">{{ item.goods.name }}</span>
                <span class="header-item">{{ item.created_at }}</span>
                <span class="header-item">{{ item.expired_at }}</span>
                <span class="header-item"  style="width: 10%">{{ item.origin_amount }} 元</span>
                <span class="header-item" v-html="item.status_label" style="width: 10%"></span>
                <div style="text-align:right;width:10%" v-if="item.status > -1">
                    <span class="last-span pay-span" @click="payOrCheckInvoice(item)" v-if="item.status === 0">
                      {{ $t('my/service:bill.Button-1') }}
                    </span>
                    <span class="last-span pay-span" @click="payOrCheckInvoice(item)"
                          v-else-if="item.status === 1 || item.status === 2 && item.is_expire === 0">
                      {{ $t('my/service:bill.Button-2') }}
                    </span>
                </div>
            </div>
            <div class="empty-data" v-if="!loading && billList.length === 0">沒有账单</div>
            <div v-else style="margin-top: 0.1rem;float: right;">
              <a-pagination v-model="current" :total="total" show-less-items @change="changePage"/>
            </div>
        </div>
        <div class="manage-bill-service-detail-box-mobile" v-if="!isPc && selectedItem">
            <span class="plan-title">{{ selectedItem.shop_name }}
                <span class="plan-number">(ID:#{{ selectedItem.shop_id }})</span>
            </span>
            <div class="plan-expired-date">
                <span style="color: red;" v-if="currentTime > parseInt(selectedItem.expired_at)">已过期</span>
                <span
                    v-if="parseInt(currentTime) < parseInt(selectedItem.expired_at)">{{ formatDate(selectedItem.expired_at) + ' 到期' }}</span>
            </div>
            <a-button type="primary" class="renew-btn" :loading="iconLoading" @click="goToRenew">{{
                    checkExpired ? '立即续费'
                        : '提前续费'
                }}
            </a-button>
            <div class="card-box-bg flow-detail-box">
                <div class="flow-left">
                    <span class="number-span">{{ selectedItem.also_traffic }} <span
                            class="unit-span">GB</span></span>
                    <span class="sec-row">剩余流量</span>
                    <span
                        class="reset-date">{{ selectedItem.reset_time }} 重置</span>
                </div>
                <div class="flow-left flow-right">
                    <span
                        class="number-span">{{ selectedItem.speed_limit ? selectedItem.speed_limit : '∞ ' }}
                                   <span class="unit-span">M</span></span>
                    <span class="sec-row">当前限速</span>
                    <span
                        class="limit-device">限制设备 不限</span>
                </div>
            </div>
            <div class="card-box-bg flow-detail-box bill-detail-box">
                <div class="flow-left" style="width: 100%">
                    <span class="number-span">{{ selectedItem.money }} <span
                            class="unit-span">元</span></span>
                    <span class="sec-row">下次续费</span>
                    <span class="reset-date">{{ selectedItem.expired_at }}</span>
                </div>
            </div>
        </div>
        <div class="my-bill-box-mobile" v-if="!isPc">
            <span class="title">我的账单</span>
            <div class="item-row-box" v-for="(item,index) in billList" :key="index">
                <div class="first-row">
                    <div class="first-row-left">
                        <span class="left-top-span">{{ payStatus[item.status] }}</span>
                        <span class="left-bottom-span">{{ item.origin_amount }}<span
                            class="left-bottom-unit">元</span></span>
                    </div>
                    <span class="unpay-span" :class="{'pay-span':item.status !== 'Paid'}"  v-if="item.status > -1"
                          @click="payOrCheckInvoice(item)">{{ item.status === 'Paid' ? '查看' : '支付' }}</span>
                </div>
                <a-divider class="divider-line"/>
                <div class="sec-row"><span class="row-left">账单编号</span><span class="row-right">{{ item.sn }}</span>
                </div>
                <div class="sec-row"><span class="row-left">商品名称</span><span class="row-right">{{ item.goods.name }}</span>
                </div>
                <div class="sec-row"><span class="row-left">生成日期</span><span
                    class="row-right">{{ item.created_at }}</span>
                </div>
            </div>
            <div class="empty-data" v-if="!loading && billList.length === 0">沒有账单</div>
        </div>
    </div>
</template>

<script>
import {AU_SELECTED_SERVICE} from "@/store/Global";

export default {
    name: "ManageBill",
    data() {
        return {
            isLoading: true,
            isPc: this.isPc(),
            sortType: '',
            sortBy: '',
            billList: [],
            status: [],
            statusList: [{type: '全部', ischecked: false}, {
                type: '开启',
                status: 'Active',
                ischecked: false
            }, {type: '停用', status: 'Inactive', ischecked: false}],
            payMethods: [],
            payMethodsList: [{key: '全部', ischecked: false}, {
                key: '支付寶',
                value: 'alipay',
                ischecked: false
            }, {key: '微信支付', value: 'wepay', ischecked: false},
                {key: '银联卡', value: 'union', ischecked: false}, {key: '信用卡', value: 'visa', ischecked: false},
                {key: 'PayPal', value: 'paypal', ischecked: false}, {key: '加密貨幣', value: 'coin', ischecked: false}],
            payStatus: {
                '-1': '已关闭',
                '0': '待支付',
                '1': '已支付',
                '2': '已完成',
            },
            payStatusList: [{key: '全部', ischecked: false}, {
                key: '已支付',
                status: 'paid',
                ischecked: false
            }, {key: '待支付', status: 'unpaid', ischecked: false}],
            showMsg: false,
            selectedItem: {},
            trafficArray: [{trafficLimit: '∞', speedLimit: '∞', isAddon: false, sessionLimit: '∞'}],
            speedArray: ['速度', '∞', '', '', '', ''],
            currentTime: new Date().getTime(),
            lang: this.$i18n.locale,
            loading: true,
            selectedService: localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)) : {},
            iconLoading: false,
            selectedId: 0,
            current: 1,
            total:100,
            prepaidPlan:false
        }
    },
    created() {
        this.getMoreData()
    },
    mounted() {
        this.selectedId = this.$route.params.serviceId
    },
    methods: {
        changePage(page){
          const that = this;
          that.billList = [];
          that.loading = true;
          this.request.getAllInvoices({page: page}).then( res =>  {
            that.loading = false;
            if (res.code === 1) {
              that.billList = res.data.data;
              that.current = res.data.current_page;
              that.total = res.data.total;
            }
          });
        },
        getMoreData() {
            this.getServiceInfo();
            const that = this;
            that.isLoading = false
            this.request.getAllInvoices({}).then( res =>  {
                that.loading = false;
                if (res.code === 1) {
                    that.billList = res.data.orderList.data;
                    that.current = res.data.orderList.current_page;
                    that.total = res.data.orderList.total;
                    that.prepaidPlan = res.data.prepaidPlan;
                }
            });
        },
        checkExpired() {
            return this.selectedItem.expired_at !== undefined &&
                (parseInt(this.selectedItem.expired_at) - this.currentTime <= 7 * 24 * 3600)
        },
        showConfirm(item){
          let that = this;
          that.$confirm({
            title: that.$t('my/service:bill.Text-9'),
            content: that.$t('my/service:bill.Text-10'),
            okText: '确认',
            cancelText: '取消',
            onOk() {
              that.request.activePrepaid().then( res => {
                    if(res.code === 1){
                      that.$message.success(res.msg);
                      setTimeout(()=>{
                        window.location.reload();
                      }, 1500);
                    } else {
                      that.$message.error(res.msg);
                    }
               });
            },
            onCancel() {},
          });
        },
        payOrCheckInvoice(item) {
            if (this.isPc) {
                let routeUrl = this.$router.resolve({
                    path: 'user/invoice/:invoiceId',
                    name: 'invoiceDetail',
                    params: {
                        invoiceId: item.id,
                    }
                });
                window.open(routeUrl.href, this.$inApp ? '_self' : '_blank');
            } else {
                this.$router.push({
                    path: 'user/invoice/:invoiceId',
                    name: 'invoiceDetail',
                    params: {
                        invoiceId: item.id,
                    },
                    query: this.$route.query
                });
            }
        },
        goToRenew() {
            this.iconLoading = true;
            const that = this;
            that.$router.push({path: '/order/place'});
        },
        formatDate(value) {
            let date = new Date(value);
            let y = date.getFullYear();
            let MM = date.getMonth() + 1;
            MM = MM < 10 ? ('0' + MM) : MM;
            let d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            return y + '-' + MM + '-' + d;
        },
        getServiceInfo() {
            this.trafficArray = [{trafficLimit: '0', speedLimit: 0, isAddon: false, sessionLimit: '∞'}];
            this.speedArray = ['速度', '', '', '', '', ''];
            let that = this;
            this.request.getAllServices().then( res => {
                if (res.code === 1) {
                    that.selectedItem = res.data;
                } else {
                    that.infoError = true;
                }
            });
        },
        fraudOrder() {
            //const that = this;
            this.request.auFraudOrder('', '', function (data) {
                console.log(data);
            });
        }
    }
}
</script>

